// --- Custom Interfaces ---
type ChatMode = "Auto" | "Conversation" | "Syllabus" | "Quiz";

// --- API Helper Function (Move to a dedicated file in production) ---
async function fetchSessionEvents(user_id: string, session_id: string) {
  if (!session_id || session_id === "new") return [];

  // URL matches your backend: /api/sessions/{user_id}/{session_id}
  const res = await fetch(
    `http://127.0.0.1:8000/api/sessions/${user_id}/${session_id}`
  );

  if (!res.ok) {
    console.error(
      "Failed to fetch session events:",
      res.status,
      res.statusText
    );
    return [];
  }

  const json = await res.json();
  return json.events || []; // Backend returns { events: [...] }
}
// -------------------------------------------------------------------

export default function Page() {
  const pathname = usePathname();
  const chatName = decodeURIComponent(pathname.replace(/^\/chat\/?/, ""));

  const [input, setInput] = useState("");
  const [mode, setMode] = useState<ChatMode>("Auto");
  const [tokenUsage, setTokenUsage] = useState(0);
  const [isStreaming, setIsStreaming] = useState(false); // For WebSocket state

  const chatModes: ChatMode[] = ["Auto", "Conversation", "Syllabus", "Quiz"];
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const wsRef = useRef<WebSocket | null>(null);

  // Get store actions and state
  const { currentChat, setCurrentChat, addMessage } = useChatStore();

  // Default user ID for testing purposes
  const userId = useUserStore.getState().user?.id || "1";

  // --- Session Message Loader (Runs when currentChat changes) ---
  useEffect(() => {
    if (!currentChat || currentChat.messages.length > 0) return;

    const loadEvents = async () => {
      try {
        // Pass guaranteed string IDs
        const events = await fetchSessionEvents(userId, String(currentChat.id));

        // Update the messages array of the current chat in the store
        if (currentChat && events.length > 0) {
          events.forEach((message: Message) => {
            addMessage(currentChat.id, message);
          });
        }
      } catch (err) {
        console.error("Failed to fetch session events:", err);
      }
    };

    loadEvents();
  }, [currentChat, userId, addMessage]);

  // --- Handlers ---

  //  TEXT INPUT HANDLER
  const handleSend = async () => {
    if (!input.trim()) return;

    const userMessage: Message = { role: "user", content: input };
    const messageToSend = input;

    // Optimistically update UI with user message
    if (currentChat) {
      addMessage(currentChat.id, userMessage);
    }

    setInput(""); // Clear input immediately
    setTokenUsage((prev) => prev + Math.ceil(messageToSend.length / 4));

    // Determine session ID for the backend call
    const sessionId = currentChat?.id || null;
    const isNewChat = !currentChat;

    try {
      // 1. Send request to the FastAPI chat endpoint
      const response = await fetch("http://127.0.0.1:8000/api/chat", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          user_id: userId,
          message: messageToSend,
          session_id: sessionId,
        }),
      });

      if (!response.ok) throw new Error("Agent response failed.");

      const data = await response.json(); // { user_id, session_id, response }

      const agentMessage: Message = {
        role: "assistant",
        content: data.response.trim(),
      };

      // 2. Update store state
      if (isNewChat) {
        // Need to fetch the full session info to populate actor/title
        // For simplicity, we create a placeholder session immediately
        const newSessionData = {
          id: data.session_id,
          title: messageToSend.substring(0, 30) + "...", // Temp title
          actor: "teacher_agent",
          time: new Date().toISOString(),
          messages: [userMessage, agentMessage], // Include both messages
        };
        // Note: The backend saves the real summary title; you might
        // want to refresh your session list here to get the new title.
      } else {
        addMessage(data.session_id, agentMessage);
      }
    } catch (error) {
      console.error("Error sending message:", error);
      // Revert optimistic update or add error message
      if (!isNewChat && currentChat) {
        addMessage(currentChat.id, {
          role: "assistant",
          content: "Li: Khng th kt ni vi Agent.",
        });
      }
    }
  };

  const handleModeSelect = (selected: ChatMode) => setMode(selected);

  const handleUploadClick = () => fileInputRef.current?.click();

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) console.log("File uploaded:", file);
  };

  //  CAMERA/WEBSOCKET HANDLER
  const handleCameraCapture = async () => {
    if (!currentChat) {
      alert("Vui lng chn hoc bt u cuc tr chuyn trc.");
      return;
    }

    if (isStreaming) {
      // Stop streaming
      wsRef.current?.close(1000, "User initiated close");
      setIsStreaming(false);
      return;
    }

    const sessionId = currentChat.id === "new" ? "new" : currentChat.id;

    // URL matches your backend: /ws/video-stream/{user_id}/{session_id}
    const wsUrl = `ws://127.0.0.1:8000/ws/video-stream/${userId}/${sessionId}`;

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true });
      const ws = new WebSocket(wsUrl);
      wsRef.current = ws;

      ws.onopen = () => {
        console.log("WebSocket connected. Starting stream.");
        setIsStreaming(true);

        // Example: Capture and send frames every 100ms
        const videoTrack = stream.getVideoTracks()[0];
        const imageCapture = new (window as any).ImageCapture(videoTrack);

        const sendFrame = async () => {
          if (ws.readyState === WebSocket.OPEN) {
            try {
              const bitmap = await imageCapture.grabFrame();
              const canvas = document.createElement("canvas");
              canvas.width = bitmap.width;
              canvas.height = bitmap.height;
              const ctx = canvas.getContext("2d");
              ctx?.drawImage(bitmap, 0, 0);

              // Convert canvas to JPEG blob and send as binary
              canvas.toBlob(
                (blob) => {
                  if (blob) ws.send(blob);
                },
                "image/jpeg",
                0.8
              );

              setTimeout(sendFrame, 100); // 10 FPS
            } catch (err) {
              console.error("Error sending frame:", err);
            }
          }
        };
        sendFrame();
      };

      ws.onmessage = (event) => {
        const data = JSON.parse(event.data);
        if (data.type === "text_analysis" && currentChat) {
          // Display agent's response in the chat area
          addMessage(currentChat.id, {
            role: "assistant",
            content: data.data,
          });
        }
      };

      ws.onerror = (e) => {
        console.error("WebSocket Error:", e);
        setIsStreaming(false);
      };

      ws.onclose = () => {
        console.log("WebSocket closed.");
        stream.getTracks().forEach((track) => track.stop()); // Stop camera
        setIsStreaming(false);
      };
    } catch (err) {
      console.error("Camera access or WebSocket failed:", err);
      alert("Khng th truy cp camera hoc kt ni.");
    }
  };

  // --- UI ---
  return (
    <SidebarProvider
      style={{ "--sidebar-width": "350px" } as React.CSSProperties}
    >
      <AppSidebar />
      <SidebarInset>
        {/* Header (omitted for brevity) */}
        <header className="bg-background sticky top-0 flex items-center w-full gap-2 border-b p-4">
          {/* ... (Header content is unchanged) ... */}
        </header>

        {/* Chat Hero Section (Main Display Area) */}
        <div className="flex-1 overflow-y-auto p-4">
          <div className="flex-1 overflow-y-auto p-4 flex items-center justify-center h-full">
            {currentChat ? (
              //  Display Chat Area when a chat is selected
              <ChatArea messages={currentChat.messages} />
            ) : (
              <div className="flex flex-col items-center">
                {/* ... (Empty chat hero content is unchanged) ... */}
              </div>
            )}
          </div>
        </div>

        {/* Chat Input Section */}
        <div className="sticky bottom-0 left-0 right-0 p-4 z-50">
          <div className="max-w-3xl w-full mx-auto mb-[20px]">
            <InputGroup>
              <InputGroupTextarea
                placeholder="Bt u cuc tr chuyn vi Miniwave..."
                value={input}
                onChange={(e) => setInput(e.target.value)}
                rows={3}
                className="resize-none"
              />

              <InputGroupAddon
                align="block-end"
                className="flex items-center gap-2"
              >
                {/* Upload Button (omitted for brevity) */}
                {/* Camera Button */}
                <Tooltip>
                  <TooltipTrigger asChild>
                    <InputGroupButton
                      variant="ghost"
                      size="sm"
                      onClick={handleCameraCapture}
                      //  UI: Change color/icon based on streaming state
                      className={
                        isStreaming ? "text-red-600 hover:bg-red-100" : ""
                      }
                    >
                      <Camera className="w-6 h-6" />
                    </InputGroupButton>
                  </TooltipTrigger>
                  <TooltipContent>
                    {isStreaming ? "Ngng camera" : "M camera"}
                  </TooltipContent>
                </Tooltip>
                {/* ... (Mode Selector, Token Count, Send button) ... */}

                {/* Token Count */}
                <InputGroupText className="ml-auto text-sm text-gray-500">
                  {tokenUsage} tokens  dng
                </InputGroupText>

                <Separator orientation="vertical" className="!h-6" />

                {/* Send */}
                <Tooltip>
                  <TooltipTrigger asChild>
                    <InputGroupButton
                      variant="default"
                      className="rounded-full p-2 flex items-center justify-center bg-[#F66868]"
                      onClick={handleSend}
                      disabled={!input.trim() || isStreaming} // Disable text input while streaming
                    >
                      Gi
                      <SendHorizonal className="ml-1 w-5 h-5" />
                    </InputGroupButton>
                  </TooltipTrigger>
                  <TooltipContent>Gi yu cu</TooltipContent>
                </Tooltip>
              </InputGroupAddon>
            </InputGroup>
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  );
}
